.form {
  display: flex;
  flex-direction: column;

  &__item {
    width: 100%;
    height: 48px;
    padding: 17px;
    margin-bottom: 16px;

    font-family: inherit;
    font-size: $main-font-size-text;

    background-color: $input-background-color;
    border: 1px solid #fff;
    border-radius: $border-radius;

    &--textarea {
      height: 144px;
      resize: none;
    }

    &::placeholder {
      color: $placeholder-text-color;
      font-size: $gray-text-size;
    }

    &:focus {
      outline-color: $primary-color;
    }
  }

  &__btn {
    width: 100%;
    margin-top: 32px;
    color: $white-text-color;
    background-color: $primary-color;
    box-shadow: 0 6px 23px rgba(0, 117, 255, 0.46);

    @include hover(background-color, $primary-darker-color);
    @include hover(box-shadow,  0 13px 27px rgba(0, 83, 181, 0.46));
  }
}
