.nav {

  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;

    list-style: none;
  }

  &__item {
    padding: 8px 24px;

    &--active {
      background-color: rgba(255,255,255,0.1);
      border-radius: $border-radius;
    }

    @include hover(color, $hover-color);
  }

  &__link {
    text-decoration: inherit;
    color: inherit;
    white-space: nowrap;
  }
}

@media screen and (max-width: $medium-screen) {
  .nav {
    &__list {
      font-size: 10px;
    }

    &__item {
      padding: 8px 15px;
    }
  }
}

@media screen and (max-width: $i-pad-screen) {
  .nav {
    &__list {
      flex-direction: column;
      font-size: 14px;
    }

    &__item {
      margin-top: 15px;
    }
  }

}
